@tailwind base;
.brand {
  @apply text-3xl font-medium text-indigo-500;
}

main {
  padding-top: 58px;
  overflow: hidden;
}
@screen md {
  main {
    padding-top: 88px;
  }
}
main .elementor-section.elementor-section-boxed > .elementor-container {
  @apply container mx-auto lg:px-10 xl:px-16;
}

.page-title {
  @apply font-display text-primary text-2xl;
  letter-spacing: 0.2em;
}

.paragraph img {
  max-width: 100%;
}
.paragraph ul {
  @apply list-disc my-3;
}
.paragraph ol {
  @apply list-decimal;
}
.paragraph ul li,
.paragraph ol li {
  @apply ml-5;
}
.paragraph strong {
  @apply font-bold;
}

.paragraph {
  @apply font-display font-normal;
}
.paragraph h1, .paragraph .h1, .paragraph heading-1 {
  @apply text-h1 font-display text-black font-bold capitalize leading-tight;
  margin-bottom: 20px;
}
.paragraph h2, .paragraph .h2, .paragraph heading-2 {
  @apply text-h2 font-display text-OrangeAlt uppercase leading-tight;
  letter-spacing: 4px;
  font-weight: 800;
  margin-bottom: 20px;
}
.paragraph h3, .paragraph .h3, .paragraph heading-3 {
  @apply text-h3 font-display font-semibold text-altRed uppercase leading-tight;
  letter-spacing: 4px;
  margin-bottom: 20px;
}
.paragraph h4, .paragraph .h4, .paragraph heading-4 {
  @apply text-h4 font-display font-bold text-themeBrown uppercase leading-tight;
  letter-spacing: 4px;
  margin-bottom: 20px;
}
.paragraph blockquote {
  padding: 40px;
  background-image: linear-gradient(90deg, #F2A341 0%, #C32B27 100%);
  font-size: 20px;
  @apply font-semibold capitalize leading-normal text-white rounded-lg;
}
.paragraph blockquote p:before {
  content: '" ';
}
.paragraph blockquote p:after {
  content: ' "';
}
.paragraph ul {
  @apply list-disc my-3;
}
.paragraph ol {
  @apply list-decimal;
}
.paragraph ul li,
.paragraph ol li {
  @apply ml-5;
}
.paragraph a {
  @apply text-hyperlink font-bold;
}
.paragraph a:hover {
  @apply text-primary;
}
.paragraph strong {
  @apply font-bold;
}
.paragraph p {
  @apply my-3;
}

.slick-next {
  right: 10px !important;
}
@screen xl {
  .slick-next {
    right: 40px !important;
  }
}
@screen lg {
  .slick-next {
    right: 20px !important;
  }
}

.slick-prev {
  left: 10px !important;
  z-index: 10;
}
@screen xl {
  .slick-prev {
    left: 40px !important;
  }
}
@screen lg {
  .slick-prev {
    left: 20px !important;
  }
}

.slick-current {
  z-index: 10;
}

.svg {
  fill: none;
}

@tailwind components;
@tailwind utilities;
.elementor-section-preview {
  @apply py-10 bg-gray-100 flex flex-col justify-center items-center text-center h-96;
}
.elementor-section-preview h1 {
  @apply text-center text-3xl font-bold text-primary;
}
.elementor-section-preview label {
  @apply text-center font-semibold;
  color: red;
}